import React from "react";
import Image from "next/image";
import Link from "next/link";

const subjects = [
  { name: "Maths", image: "/subjects/maths.png", href: "/junior-grades/maths" },
  { name: "Science", image: "/subjects/science.png", href: "/junior-grades/science" },
  { name: "History", image: "/subjects/history.png", href: "/junior-grades/history" },
  { name: "Chemistry", image: "/subjects/chemistry.png", href: "/junior-grades/chemistry" },
  { name: "English", image: "/subjects/english.png", href: "/junior-grades/english" },
  { name: "ICT", image: "/junior-grades/ict.png", href: "/junior-grades/ict" },
];

const JuniorGradesSection= () => {
  return (
    <section className="py-20 bg-[var(--color-light)]">
      <div className="max-w-7xl mx-auto px-6 grid lg:grid-cols-2 gap-16 items-center">
        
        <div>
          <span className="inline-block mb-4 px-4 py-1 rounded-full bg-[var(--color-primary)] text-white text-sm font-medium">
            Junior Grades
          </span>

          <h2 className="text-4xl font-bold text-[var(--color-dark)] mb-6">
            Grade 6 to 11 Learning Program
          </h2>

          <p className="text-lg text-gray-600 mb-8 max-w-xl">
            Structured subject-wise learning designed for junior students.
            Explore core academic and ICT subjects with clear explanations
            and guided materials.
          </p>

          <Image
            src="/images/junior-learning.png"
            alt="Junior Grades Learning"
            width={520}
            height={360}
            className="rounded-2xl shadow-card"
          />
        </div>

        <div className="grid grid-cols-2 sm:grid-cols-3 gap-6">
          {subjects.map((item) => (
            <Link
              key={item.name}
              href={item.href}
              className="group bg-white rounded-2xl shadow-card p-6 flex flex-col items-center justify-center transition hover:-translate-y-1 hover:shadow-card-hover"
            >
              <div className="w-14 h-14 mb-4 relative">
                <Image
                  src={item.image}
                  alt={item.name}
                  fill
                  className="object-contain"
                />
              </div>

              <span className="text-sm font-semibold text-[var(--color-dark)] group-hover:text-[var(--color-primary)] transition">
                {item.name}
              </span>
            </Link>
          ))}
        </div>

      </div>
    </section>
  );
};

export default JuniorGradesSection;
